.container {
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: content;
    align-items: center;
    margin: auto auto;
}

.schoolBlock {
    width: 72%;
    height: 120px;
    background: #333;
    box-shadow: 3px 2px 2px rgb(4, 14, 20, 0.3);
    /* margin: 0 auto; */
    margin-bottom: 16px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    color: #fff;
    position: relative;
    overflow: hidden;

}

.schoolBlock > h3 {
    max-width: 200px;
    font-family: Montserrat, sans-serif;
    color: var(--first-color);
}

.schoolBlock > img {
    width: 250px;
    height : 100%;
}

.schoolBlock:first-child {
    margin-top: 5%;
}

@media only screen and (min-width: 46.25em) and (max-width: 63.9375em) {
    .container {
        margin-top: 10%;
    }
    .schoolBlock {
        height: 100px;
        width: 92%;
    }
}

/* Mobile */

@media only screen and (max-width: 46.1875em) {
    .schoolBlock {
        height: 80px;
        width: 94%;
        margin: 3% 0;
    }
    .schoolBlock > img {
        display: none;
    }
}

